.header {
    display: flex;
    justify-content: space-between;
    align-items: center;
    padding: 10px 20px;
    background-color: #fff;
    box-shadow: 0 2px 5px #fff;
  }
  .menu {
    display: flex;
    gap: 15px;
  }
  
  .menu a {
    text-decoration: none;
    color: #333;
    font-weight: bold;
  }
  
  .searchContainer input {
    padding: 5px;
    color: #000000;
    border: 1px solid #000000;
    background-color: #fff;
    border-radius: 5px;
  }